import React,  {useState, useEffect} from 'react';
import './index.css';

import Movie from '../../container/components/Movie';

const FEATURE_API = "https://api.themoviedb.org/3/discover/movie?sort_by=popularity.desc&api_key=11b2baad29ed34e1934b17a64050d900";

const SEARCH_API = "https://api.themoviedb.org/3/search/movie?api_key=11b2baad29ed34e1934b17a64050d900&query= ";

function App()  {
    const [movies, setMovies] = useState([]);
    const [searchTerm, setSearchTerm] = useState("");

    const getMovies = (API) => {
        fetch(API)
        .then((res) => res.json())
        .then((data) => setMovies(data.results));

    }
    useEffect(() => {
       getMovies(FEATURE_API);
    

      
    },[]);

    const handleOnSubmit = (event) =>{
        event.preventDefault();

        if(searchTerm) {
            getMovies(`${SEARCH_API}${searchTerm}`)
            setSearchTerm("");
        }
    };
   const handleOnchange = (event) => {
       setSearchTerm(event.target.value);
   }

    return(
    <>
     <p>ReactMovies</p>
    <header>
       
        <form onSubmit = {handleOnSubmit}>
            <input 
            type = "search"
            className="search"
            placeholder = "Search..."
            value={searchTerm}
            onChange = {handleOnchange}
            />

        </form>

    </header>
    <div className="movie-container">
        {movies.length > 0 &&
          movies.map((movie) => <Movie key={movie.id} {...movie} />)}
      </div>;
      
    </>
    );
}

export default App;